@import "variables";

.information {
    position: relative;
    padding-top: 25px;
    padding-bottom: 25px;

    > picture {
        z-index: -1;
    }
    
    > header {
        display: none;
    }

    &__designer {
        position: relative;
        margin-top: -53px;
        margin-left: auto;
        margin-right: 14px;
        z-index: 2;
        max-width: 268px;
        background: #181719;
        box-shadow: 0px 4px 4px rgba(0,0,0,.25);
        padding-top: 7px;
        padding-left: 22px;
        padding-bottom: 25px;
    
        > a {
            color: white;
    
            > p {
                max-width: 210px;
                font-family: "Lora", Arial, Helvetica, sans-serif;
                font-weight: bold;
                font-size: 18px;
                padding-top: 10px;
            }
        }

        &__address {
            @include flex-row();
            align-items: center;
            max-width: 160px;
        
            > img {
                border-radius: 50%;
                width: 50px;
            }

            &--desc {

                > p {
                    font-weight: 500;
                    font-size: 12px;
                    line-height: .7;
                }
                
                > span {
                    color: #828282;
                    font-weight: 500;
                    font-size: 10px;
                    line-height: .5;
                }
            }
        }

    }
}
